// @import './reset.scss';


$메인칼라: black;



.wow-enter {
  opacity: 0;
}
.wow-enter-active {
  opacity: 1;
  transition: all 500ms;
}




.black {
  color: $메인칼라;
}
@mixin 함수() {
  background: #eeeeee;
  padding: 20px;
  border-radius: 20px;
  max-width: 500px;
  width: 100%;
  margin: auto;
}

.my-alert {
  @include 함수()
}

.my-alert2 {
  @extend.my-alert;
  background: #ffe591;
}






.my-alert p {
  margin-bottom: 0;
}

